.footer {
  &__wrapper {
    position: relative;
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
    &:before {
      @include screen(tablet) {
        position: absolute;
        content: '';
        top: 0;
        left: -20px;
        width: calc(100% + 40px);
        height: 1px;
        background-color: $gray;
        opacity: 0.5;
      }
    }
  }
  &__links {
    display: flex;
    flex-direction: column;
    position: relative;
    @include screen(tablet) {
      grid-column: 5 / span 3;
	    grid-row: 2;
    }
    @include screen(laptop) {
      grid-column: 5 / span 4;
    }
    &:before {
      @include screen(tablet) {
        position: absolute;
        content: '';
        height: 100%;
        width: 1px;
        top: 0;
        bottom: 0;
        right: 0;
        background-color: $gray;
        opacity: 0.5;
      }
    }
  }
  &__link {
    display: inline-block;
    padding: 18px 0;
    position: relative;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    @include screen(tablet) {
      padding: 18px 20px;
    }
    @include screen(laptop) {
      font-size: 14px;
      line-height: 18px;
    }
    &:last-child {
      @include screen(tablet) {
        &:before {
          display: none;
        }
      }
    }
    &:before {
      position: absolute;
      content: '';
      bottom: 0;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(tablet) {
        left: -0;
        width: 100%;
        right: 0;
      }
    }
  }
  &-bottom {
    padding: 30px 0;
    position: relative;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    @include screen(tablet) {
      grid-column: 1 / span 12;
      grid-row: 3;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
    @include screen(tablet) {
      font-size: 16px;
      line-height: 21px;
    }
    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
    }
    &__policy {
      border-bottom: 1px solid $black-400;
      max-width: max-content;
    }
    &__text {
      display: flex;
      align-items: center;
      gap: 13px;
    }
  }
  &-contacts {
    position: relative;
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    gap: 50px;

    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    @include screen(tablet) {
      grid-column: 1 / span 4;
	    grid-row: 1 / span 2;
      gap: 0;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
    &:before {
      position: absolute;
      content: '';
      top: 0;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(tablet) {
        bottom: 0;
        width: 1px;
        height: 100%;
        left: auto;
        right: 0;
      }
    }
    &__logo {
      display: none;
      @include screen(tablet) {
        display: block;
        width: 96px;
        height: 26px;
      }
      @include screen(laptop) {
        width: 125px;
        height: 34px;
      }
    }
    &__top {
      display: flex;
      flex-direction: column;
      gap: 10px;
      @include screen(tablet) {
        margin-top: 35px;
      }
      @include screen(laptop) {
        margin-top: 40px;
      }
    }
    &__item {
      display: flex;
      align-items: center;
      gap: 7px;
      max-width: max-content;
    }
    &__bottom {
      @include screen(tablet) {
        margin-top: 50px;
      }
      @include screen(laptop) {
        margin-top: 160px;
      }
    }
    &__socials {
      margin-top: 8px;
      display: flex;
    }
    &__social {
      width: 21px;
      height: 21px;
    }
  }
  &-callback {
    padding: 30px 0;
    @include screen(tablet) {
      grid-column: 8 / span 5;
      grid-row: 2;
      padding: 20px 0 20px 20px;
    }
    @include screen(laptop) {
      padding: 40px 0 40px 20px;
      grid-column: 9 / span 4;
    }
    h2 {
      br {
        @include screen(laptop) {
          display: none;
        }
      }
    }
    &__form {
      margin-top: 16px;
    }
    &__inputs {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
    &__input {
      background-color: $white;
      display: flex;
      align-items: center;
      position: relative;
      width: 100%;
      input {
        padding: 16px 20px;
        font-size: 14px;
        line-height: 18px;
        color: $black-400;
        height: 40px;
        width: 100%;
        border: 1px solid rgba($gray, 0.5);
        outline: none;
        &::placeholder {
          font-family: 'Jost';
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
        }
      }
    }
    &__text {
      margin-top: 10px;
      font-size: 12px;
      line-height: 14px;
      opacity: 0.6;
      a {
        text-decoration: underline;
      }
    }
    &__button {
      margin-top: 24px;
    }
  }
  &-mailing {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    align-items: center;
    position: relative;
    @include screen(tablet) {
      grid-column: 5 / span 8;
	    grid-row: 1;
      display: flex;
      gap: 25px;
      padding: 20px 0 20px 20px;
    }
    @include screen(laptop) {
      gap: 213px;
    }
    &:before {
      position: absolute;
      content: '';
      bottom: 0;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(tablet) {
        left: 0;
        width: calc(100% + 20px);
      }
    }
    &__img {
      display: block;
      width: 136px;
      height: 36px;
      @include screen(tablet) {
        display: none;
      }
    }
    &__wrapper {
      display: flex;
      flex-direction: column;
      gap: 15px;
      @include screen(tablet) {
        flex-direction: row;
      }
    }
    span {
      display: block;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      white-space: nowrap;
      text-transform: uppercase;
      text-decoration: underline;
      @include screen(tablet) {
        font-size: 14px;
        line-height: 18px;
        text-decoration: none;
      }
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    &__form {
      grid-column: span 2;
      @include screen(tablet) {
        width: 100%;
      }
    }
    &__input {
      background-color: $white;
      display: flex;
      align-items: center;
      position: relative;
      width: 100%;
      input {
        padding: 10px 30px 10px 0;
        font-size: 14px;
        line-height: 18px;
        color: $black-400;
        height: 40px;
        width: 100%;
        border-bottom: 1px solid rgba($gray, 0.5);
        outline: none;
        &::placeholder {
          font-family: 'Jost';
          font-size: 16px;
          font-weight: 400;
          line-height: 21px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: $black-400;
        }
        @include screen(laptop) {
          font-size: 16px;
          line-height: 21px;
        }
      }
      svg {
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
      }
    }

  }
}