.card-new {
  border: 1px solid $gray-300;
  background-color: $white;
  display: inline-block;
  @include screen(laptop) {
    width: 100%;
    transition: border-color;
  }
  &:hover {
    @include screen(laptop) {
      border-color: $black-400;
    }
  }
  &__top {
    padding: 12px 10px 0 10px;
    @include screen(tablet) {
      padding: 20px 10px 0 10px;
    }
  }
  &__statuses {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__status {
    font-size: 12px;
    line-height: 14px;
    padding: 3px 7px;
    @include screen(laptop) {
      padding: 3px 10px;

    }
    &-transparent {
      color: $gray-400;
      border: 1px solid $gray-400;
    }
    &-yellow {
      background-color: $yellow;
    }
  }
  &__image {
    margin: 20px auto 0;
    max-width: 42px;
    max-height: 144px;
    @include screen(tablet) {
      max-width: 61px;
      max-height: 211px;
    }
    @include screen(laptop) {
      margin: 30px auto 0;
      max-width: 90px;
      max-height: 314px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__center {
    border: 1px solid $gray-300;
    border-width: 1px 0;
    padding: 5px 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
    @include screen(tablet) {
      padding: 8px 45px;
      border: none;
    }
    span {
      text-transform: uppercase;
      font-size: 12px;
      line-height: 14px;
      color: $gray;
    }
  }
  &__bottom {
    padding: 20px 10px 10px 10px;
    @include screen(tablet) {
      padding: 10px;
    }
    @include screen(laptop) {
      padding: 20px 20px 15px 20px;
    }
  }
  &__brand,
  &__name {
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
  }
  &__brand,
  &__name,
  &__amount {
    max-width: 120px;
    @include screen(tablet) {
      max-width: 100%;
    }
  }
  &__brand {
    color: $gray;
  }
  &__name {
    letter-spacing: 0.02em;
    margin-top: 5px;
    @include screen(tablet) {
      font-size: 14px;
      line-height: 18px;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__amount {
    margin-top: 12px;
    font-size: 12px;
    line-height: 14px;
    color: $gray;
    @include screen(tablet) {
      margin-top: 10px;
    }
  }
}