.stores {
  padding-top: 10px;
  padding-bottom: 60px;
  @include screen(tablet) {
    padding-top: 16px;
  }
  @include screen(laptop) {
    padding-top: 24px;
    padding-bottom: 140px;
  }
  &__text {
    max-width: 204px;
    font-size: 14px;
    line-height: 18px;
    @include screen(tablet) {
      max-width: max-content;
    }
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__buttons {
    margin-top: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    @include screen(tablet) {
      margin-top: 0;
      gap: 10px;

    }
  }
  &__filter {
    margin-top: 32px;
    display: flex;
    justify-content: flex-end;
    @include screen(tablet) {
      margin-top: 0;
    }
  }
  &__container {
    @include screen(tablet) {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 24px;
      padding: 25px 20px;
      &:before,
      &:after {
        position: absolute;
        content: '';
        height: 1px;
        background-color: $gray;
        opacity: 0.5;
        left: 0;
        width: 100%;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
    }
    @include screen(tablet) {
      margin-top: 32px;
    }
  }
  &__items {
    margin-top: 16px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      margin-top: 24px;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
    @include screen(laptop) {
      margin-top: 32px;
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }
  }
  &__item {
    min-height: 162px;
    border: 1px solid rgba($gray, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 10px;
    span {
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
    &:before {
      position: absolute;
      z-index: -1;
      content: '';
      width: 185px;
      height: 185px;
      background-color: transparent;
      top: 50%;
      left: 50%;
      border-radius: 50%;
      transform: translate(-50%, -50%);
    }
    @include screen(tablet) {
      min-height: 234px;
      
    }
    @include screen(laptop) {
      min-height: 224px;
      span {
        font-size: 16px;
        line-height: 21px;
        transition: color $transition-default;
      }
      &:before {
        transition: background-color $transition-default, border $transition-default;
      }
      &:hover {
        border: 1px solid rgba($black-400, 0.5);
        span {
          color: $white;
        }
        &:before {
          background-color: $black-400;
        }
      }
    }
  }
}