
.nav {
  display: grid;
  grid-template-columns: 3fr 1fr;
  background-color: $white;
  @include screen(laptop) {
    grid-template-columns: 2fr 1fr 2fr;
    height: 100%;
    width: 100%;
  }
  
  &__title {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 500;
    line-height: 18px;
    letter-spacing: 0.02em;
    padding: 10px 20px;
    border-bottom: 1px solid $gray;
    border-right: 1px solid $gray;
    @include screen(laptop) {
      padding: 30px 30px 20px 30px;
      font-size: 16px;
      line-height: 21px;

    }
  }
  &__column {
    height: 100%;
    display: grid;
    &-two {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      @include screen(laptop) {
        grid-template-columns: repeat(2, minmax(0, 1fr));
      }
    }
    &-three {
      grid-template-columns: repeat(3, minmax(0, 1fr));
      border-bottom: 1px solid $gray;
      @include screen(laptop) {
        border-bottom: none;
      }
      .nav-item {
        &:last-child {
          border: red;
        }
      }
    }
  }
  &__block {
    &-three {
      grid-column: span 2;
      .nav__title {
        background-color: $white;
        border-top: 1px solid $gray;
      }
      @include screen(laptop) {
        grid-column: span 1;
        .nav__title {
          border-top: none;
        }
      }
    }
  }
  &-item {
    border-right: 1px solid $gray;
    &-tablet {
      display: flex;
      @include screen(laptop) {
        display: none;
      }
      .nav-item__title-top {
        border-top: none;
      }
    }

    &__title {
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
      border-bottom: 1px solid $gray;
      text-decoration: underline;
      padding: 10px 20px;
      white-space: nowrap;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
        padding: 10px 10px 10px 30px;
      }
      &-top {
        border-top: 1px solid $gray;
      }
    }
    &__category {
      display: flex;
      flex-direction: column;
      width: 100%;
      &-bottom {
        @include screen(laptop) {
          padding-bottom: 50px;
          border-bottom: 1px solid $gray;
        }
      }
      &-desktop {
        display: none;
        @include screen(laptop) {
          display: flex;
        }
      }
    }
    &__links {
      padding: 10px 20px;
      display: flex;
      flex-direction: column;
      gap: 16px;
      max-width: max-content;
      width: 100%;
      @include screen(laptop) {
        gap: 13px;
        padding: 10px 10px 10px 30px;
      }
    }
  }
}