.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $black-400;
  border: 1px solid $black-400;
  width: 162px;
  padding: 16px 10px;
  transition: background-color $transition-default;
  span {
    color: $white;
    font-size: 14px;
    line-height: 18px;
  }
  &:hover {
    background-color: $black-hover;
  }
  &-transparent {
    background-color: $white;
    span {
      color: $black-400;
    }
    &:hover {
      background-color: $white;
    }
  }
  &-loading {
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
  &-small {
    padding: 10px;
    span {
      text-transform: uppercase;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    @include screen(tablet) {
      padding: 7px 10px;
      width: 140px;
    }
    @include screen(laptop) {
      width: 162px;
    }
  }
  &-none {
    span {
      text-transform: none;
    }
  }
}