.main-products {
  padding: 30px 0;
  @include screen(laptop) {
    padding: 50px 0;
  }
  h2 {
    br {
      @include screen(laptop) {
        display: none;
      }
    }
  }
  &__container {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    border: 1px solid $gray;
    overflow: hidden;
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    &-second {
      @include screen(tablet) {
        grid-template-columns: repeat(4, minmax(0, 1fr));
        width: 100%;
        grid-column: 1 / 3;
      }
      .main-products__item {
        &:nth-child(3),
        &:nth-child(4) {
          @include screen(tablet) {
            border: 1px solid $gray;
            border-width: 0 0 0 1px;
          }
        }
      }
    }
  }
  &__picture {
    height: 335px;
    border: 1px solid $gray;
    border-width: 1px 0 1px 0;
    @include screen(tablet) {
      height: 366px;
      border-width: 0 0 1px 1px;

    }
    @include screen(laptop) {
      width: 716px;
      height: 682px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__item {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 168px;
    @include screen(tablet) {
      height: 183px;
    }
    @include screen(laptop) {
      height: 341px;
    }
    &-center {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-link {
      position: relative;
      &:before {
        position: absolute;
        content: '';
        border-radius: 50%;
        width: 136px;
        height: 136px;
        border: 1px solid $gray;
        background-color: transparent;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        transition: opacity $transition-default;
        @include screen(laptop) {
          width: 256px;
          height: 256px;
        }
      }
      button {
        opacity: 0;
        transition: opacity $transition-default;
      }
      &:hover {
        &:before {
          opacity: 1;
        }
        button {
          opacity: 1;
        }
      }
      &:active {
        &:before {
          border-color: $black-400;
        }
        button {
          background-color: $black-400;
        }
      }
    }
    &-circle {
      position: relative;
      &:hover {
        &:before {
          background-color: $black-hover;
        }
      }
      &:before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        background-color: $black-400;
        width: 115px;
        height: 115px;
        z-index: 0;
        transition: background-color $transition-default;
        @include screen(tablet) {
          width: 125px;
          height: 125px;
        }
        @include screen(laptop) {
          width: 234px;
          height: 234px;
        }
      }
      span {
        color: $white;
        z-index: 1;
      }
    }
    span {
      text-transform: uppercase;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-align: center;
      @include screen(tablet) {
        font-size: 14px;
        line-height: 18px;
      }
      @include screen(tablet) {
        font-size: 16px;
        line-height: 21px;
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:nth-child(2) {
      border: 1px solid $gray;
      border-width: 0 0 0 1px;
    }
    &:nth-child(3) {
      border: 1px solid $gray;
      border-width: 1px 0 0 0;
      @include screen(tablet) {
        border-width: 1px 0 1px 0;
      }
    }
    &:nth-child(4) {
      border: 1px solid $gray;
      border-width: 1px 0 0 1px;
      @include screen(tablet) {
        border-width: 1px 0 1px 1px;
      }
    }
  }
  &__button {
    width: 30px;
    height: 30px;
    background-color: $black-400;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 30px;
    bottom: 15px;
    @include screen(tablet) {
      bottom: 25px;
      right: 35px;
    }
    @include screen(laptop) {
      width: 57px;
      height: 57px;
      right: 50px;
      bottom: 43px;
    }
    svg {
      width: 13px;
      @include screen(laptop) {
        width: 23px;
      }
    }
  }
}