.header-mobile {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  background-color: $white;
  z-index: -1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
  @include screen(laptop) {
    display: none;
  }
  &.active {
    z-index: 3000;
    visibility: visible;
    opacity: 1;
  }
  &__wrapper {
    background-color: $white;
    height: 100%;
    position: relative;
  }
  &__top {
    padding: 30px 20px;
  }
  &__controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 125px;
    height: 34px;
  }
  &__form {
    margin-top: 30px;
  }
  &__input {
    background-color: $white;
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    input {
      padding: 16px 20px;
      font-size: 14px;
      line-height: 18px;
      color: $black-400;
      height: 40px;
      width: 100%;
      border: 1px solid $gray;
      outline: none;
    }
  }
  &__icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
  }
  &__menu {
    position: relative;
    border-top: 1px solid $gray-400;
    width: 100%;
    height: 100%;
  }
  &__blocks {
    &.active {
      height: 100%;
      .header-mobile__category {
        flex-direction: row-reverse;
        justify-content: flex-end;
        span {
          margin-left: 10px;
          font-size: 14px;
          line-height: 18px;
          opacity: 0.8;
        }
      }
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__block {
    padding: 11px 20px;
    border-bottom: 1px solid $gray-400;
    &.active {
      padding: 20px;
    }
  }
  &__category {
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
      font-size: 12px;
      font-weight: 400;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
  }
  &__list {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    z-index: -20;
    overflow-y: scroll;
    height: calc(100vh - 223px);
    transition: opacity $transition-default;
    @include screen($screen-tablet) {
      display: none;
    }
    &.active {
      opacity: 1;
      visibility: visible;
      position: relative;
      z-index: 1;
    }
    &::-webkit-scrollbar {
      width: 10px;
      background-color: $gray-100;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $black-400;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding: 10px 20px;
    border-bottom: 1px solid $gray;
    &-large {
      padding: 16px 20px;
    }
    &:last-child {
      border: none;
    }
  }
  &__name {
    max-width: max-content;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    opacity: 0.8px;
    &-underline {
      font-weight: 500;
      border-bottom: 1px solid $black-400;
      opacity: 0.9px
    }
    &-uppercase {
      font-weight: 500;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      opacity: 1px;
    }
  }
}