.header {
  background-color: $white;
  width: 100%;
  // position: fixed;
  position: relative;
  // top: 0;
  // left: 0;
  z-index: 2600;
  &__wrapper {
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include screen(tablet) {
      padding: 30px 0;
    }
    @include screen(laptop) {
      padding: 20px 0;
    }
  }
  &__logo {
    width: 77px;
    height: 20px;
    @include screen(tablet) {
      width: 96px;
      height: 26px;
    }
    @include screen(laptop) {
      width: 125px;
      height: 34px;
    }
  }
  &__links {
    display: none;
    @include screen(laptop) {
      display: flex;
      gap: 30px;
    }
  }
  &__link {
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    cursor: pointer;
    border-bottom: 1px solid transparent;
    transition: border-color $transition-default, color $transition-default;
    &:hover {
      color: $black-hover;
      border-color: $black-hover;
    }
  }
  &__search {
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  &__menu {
    width: 30px;
    height: 7px;
    cursor: pointer;
    position: relative;
    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 1px;
      left: 0;
      right: 0;
      background-color: $black-400;
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
    @include screen(laptop) {
      display: none;
    }
  }
}