.post {
  padding-top: 16px;
  &__banner {
    max-height: 186px;
    overflow: hidden;
    width: 100%;
    @include screen(tablet) {
      max-height: 293px;
    }
    @include screen(laptop) {
      max-height: 563px;
    }
    img {
      height: 100%;
      object-fit: cover;
    }
  }
  &__container {
    margin-top: 10px;
    @include screen(laptop) {
      margin-top: 32px;
      display: flex;
      align-items: flex-start;
      gap: 52px;
    }
  }
  &__menu {
    padding: 15px 20px;
    border: 1px solid rgba($gray, 0.5);
    @include screen(tablet) {
      padding: 15px 25px;
    }
    @include screen(laptop) {
      padding: 25px 35px;
      min-width: 514px;
    }
    h5 {
      font-family: 'Tenor Sans';
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__links {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    @include screen(tablet) {
      margin-top: 15px;
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(laptop) {
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }
    a {
      font-size: 14px;
      line-height: 18px;
      border-bottom: 1px solid transparent;
      max-width: 250px;
      @include screen(tablet) {
        max-width: 100%;
      }
      @include screen(laptop) {
        max-width: max-content;
        font-size: 16px;
        line-height: 21px;
        transition: border $transition-default;
        &:hover {
          border-bottom: 1px solid $black-400;
        }
      }
    }
  }
  &__content {
    margin-top: 20px;
    @include screen(tablet) {
      margin-top: 50px;
    }
    @include screen(laptop) {
      margin-top: 0;
    }
    
    p {
      margin-top: 10px;
      font-size: 14px;
      line-height: 18px;
      opacity: 0.6;
      @include screen(tablet) {
        margin-top: 16px;
      }
      @include screen(laptop) {
        margin-top: 20px;
        font-size: 16px;
        line-height: 21px;
      }
      &:first-child {
        margin-top: 0;
      }
      a {
        border-bottom: 1px solid $gray;
        @include screen(laptop) {
          transition: border-bottom $transition-default, color $transition-default, opacity $transition-default;
          &:hover {
            border-bottom: 1px solid $black-hover;
            color: $black-hover;
            opacity: 1;
          }
        }
      }
    }
    h2 {
      margin-top: 40px;
      margin-bottom: 16px;
      font-family: 'Tenor Sans';
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      @include screen(tablet) {
        margin-top: 50px;
        margin-bottom: 24px;
        max-width: 320px;
      }
      @include screen(laptop) {
        margin-top: 50px;
        margin-bottom: 16px;
        font-size: 16px;
        line-height: 21px;
        max-width: 440px;
      }
    }
  }
  &__imges {
    @include screen(tablet) {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 10px;
    }
  }
  &__img {
    margin-top: 10px;
    img {
      object-fit: cover;
      width: 100%;
    }
  }
  &__socials {
    margin-top: 24px;
    display: flex;
    gap: 16px;
    @include screen(tablet) {
      margin-top: 32px;
    }
    a {
      display: inline-block;
      width: 33px;
      height: 33px;
    }
  }
}