.news-filter {
  padding-top: 30px;
  @include screen(tablet) {
    padding-top: 60px;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 30px;
    @include screen(tablet) {
      gap: 24px;
    }
    @include screen(laptop) {
      flex-direction: row;
      justify-content: space-between;
      position: relative;
      &:before,
      &:after {
        position: absolute;
        content: '';
        height: 1px;
        background-color: $gray;
        opacity: 0.5;
        left: 0;
        width: 100%;
      }
      &:before {
        top: 0;
      }
      &:after {
        bottom: 0;
      }
    }
  }
  &__items {
    padding: 15px 0;
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      display: flex;
      padding: 20px 0;
    }
    @include screen(tablet) {
      padding: 32px 0;
    }
    &:before,
    &:after {
      position: absolute;
      content: '';
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(tablet) {
        left: -0;
        width: 100%;
      }
      @include screen(laptop) {
        display: none;
      }
    }
    &:before {
      top: 0;
    }
    &:after {
      bottom: 0;
    }
  }
  &__buttons {
    @include screen(laptop) {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &__button {
    max-width: max-content;
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 8px 12px;
    border: 1px solid $black-400;
    span {
      font-size: 14px;
      line-height: 18px;
    }
  }
}