.post-others {
  padding-top: 52px;
  overflow: hidden;
  @include screen(tablet) {
    padding-top: 60px;
  }
  @include screen(laptop) {
    padding-top: 100px;
  }
  h2 {
    font-family: 'Tenor Sans';
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    max-width: 272px;
    @include screen(tablet) {
      font-size: 24px;
      line-height: 31px;
      max-width: 524px;
    }
    @include screen(laptop) {
      font-size: 28px;
      line-height: 36px;
      max-width: 618px;
    }
  }
  &__wrapper {
    .swiper {
      margin-top: 16px;
      overflow: visible;
    }
  }
}