.post-navigation {
  padding-top: 60px;
  padding-bottom: 60px;
  @include screen(laptop) {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    gap: 10px;
    @include screen(tablet) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
  }
  &__item {
    border: 1px solid rgba($gray, 0.5);
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 127px;
    @include screen(tablet) {
      min-height: 162px;
      padding: 30px;
    }
    @include screen(laptop) {
      min-height: 197px;
      padding: 40px;
    }
    &-reverse {
      flex-direction: row-reverse;
      .post-navigation__button {
        svg {
          transform: rotate(180deg);
        }
      }
      .post-navigation__content {
        text-align: left;
      }
    }
    &.active {
      .post-navigation__button {
        border: 1px solid rgba($black-400, 1);
        background-color: $black-400;
        svg {
          path {
            fill: $white;
            fill-opacity: 1;
          }
          g {
            opacity: 1;
          }
        }
      }
      @include screen(laptop) {
        transition: border-color $transition-default;
        &:hover {
          border-color: $black-400;
        }
      }
    }
  }
  &__button {
    width: 45px;
    height: 45px;
    border: 1px solid rgba($gray, 0.5);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
      width: 20px;
      @include screen(laptop) {
        width: 25px;
      }
    }
    @include screen(laptop) {
      width: 75px;
      height: 75px;
    }
  }
  &__content {
    text-align: right;
  }
  &__text {
    font-size: 14px;
    line-height: 18px;
    color: $gray;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__title {
    margin-top: 5px;
    text-transform: uppercase;
    font-family: 'Tenor Sans';
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.02em;
    max-width: 158px;
    @include screen(laptop) {
      font-size: 16px;
      line-height: 21px;
      max-width: 255px;
    }
  }
}