.main-about {
  padding: 30px 0;
  @include screen(laptop) {
    padding: 50px 0;
  }
  &__container {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    @include screen(tablet) {
      margin-top: 24px;
    }
    @include screen(laptop) {
      margin-top: 32px;
      flex-direction: row-reverse;
      gap: 20px;
    }
  }
  &__image {
    margin: 0 auto;
    @include screen(laptop) {
      min-width: 776px;
    }
  }
  &__content {
    @include screen(laptop) {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
  }
  &__text {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    @include screen(tablet) {
      gap: 16px;
    }
    @include screen(tablet) {
      gap: 40px;
    }
    p {
      font-size: 14px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
      span {
        font-weight: 500;
      }
    }
  }
  &__button {
    display: inline-block;
    margin-top: 24px;
    @include screen(tablet) {
      margin-top: 32px;
    }
  }
  &__links {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  &__link {
    display: block;
    position: relative;
    padding: 25px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include screen(laptop) {
      padding: 25px 20px;
    }
    span {
      font-family: 'Tenor Sans';
      font-size: 16px;
      line-height: 21px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
    &:before {
      position: absolute;
      content: '';
      bottom: 0;
      left: -20px;
      width: calc(100% + 40px);
      height: 1px;
      background-color: $gray;
      opacity: 0.5;
      @include screen(laptop) {
        left: -0;
        width: 100%;
        right: 0;
      }
    }
    svg {
      opacity: 0;
      visibility: hidden;
      transition: $transition-default;
    }
    &:hover {
      svg {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__items {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 5px;
    @include screen(tablet) {
      margin-top: 40px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
    @include screen(laptop) {
      margin-top: 70px;
      gap: 10px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
    min-height: 164px;
    border: 1px solid rgba($gray, 0.5);
    @include screen(tablet) {
      min-height: 176px;
      gap: 15px;
    }
    @include screen(laptop) {
      min-height: 342px;
      gap: 30px;
    }
    span {
      text-align: center;
      font-size: 14px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
  &__icon {
    width: 41px;
    height: 34px;
    @include screen(tablet) {
      width: 39px;
      height: 37px;
    }
    @include screen(laptop) {
      width: 73px;
      height: 70px;
    }
  }
}