.header-tablet {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: -20;
  overflow-y: auto;
  width: 100%;
  height: calc(100% - 236px);
  transition: opacity $transition-default;
  &::-webkit-scrollbar {
    width: 10px;
    background-color: $gray-100;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $black-400;
  }
  &.active {
    opacity: 1;
    visibility: visible;
    position: relative;
    z-index: 1;
  }
}