.hero {
  @include screen(laptop) {
    padding-top: 30px;
  }
  &__banner {
    position: relative;
    height: 596px;
    background-image: linear-gradient(90deg, #FCFAFB 0%, #FAF8F9 15%, #F7F6F4 31.3%, #F6F5F3 45.65%, #F5F3F4 62.45%, #F6F4F5 79.09%, #F8F7F5 88.99%, #F8F7F5 100%);
    @include screen(tablet) {
      background-image: linear-gradient(180deg, #FEFCFD 0%, #FBF9FA 15%, #FBF9FA 31.3%, #F9F8F6 45.65%, #F8F4F3 62.45%, #F3EFEE 79.09%, #F8F7F5 88.99%, #F8F7F5 100%);
      height: 400px;
    }
    @include screen(laptop) {
      background-image: none;
      height: 524px;
    }
  }
  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
    @include screen(laptop) {
      position: relative;
      inset: 0;
    }
  }
  &__circle {
    width: 273px;
    height: 273px;
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    circle {
      stroke: $green;
      @include screen(laptop) {
        stroke: url(#paint0_linear_351_68);
      }
    }
    @include screen(tablet) {
      top: 29px;
      left: 90px;
      width: 309px;
      height: 309px;
      transform: translateX(0);
    }
    @include screen(laptop) {
      left: 200px;
      top: 50%;
      transform: translateY(-50%);
      width: 433px;
      height: 433px;
    }
  }
  &__content {
    position: absolute;
    z-index: 1;
    top: 56px;
    left: 25px;
    max-width: 256px;
    @include screen(tablet) {
      top: 78px;
      left: 50px;
      max-width: 354px;
    }
    @include screen(laptop) {
      max-width: 494px;
      top: 50%;
      left: 122px;
      transform: translateY(-50%);
    }
  }
  &__title {
    font-family: 'Tenor Sans';
    font-size: 32px;
    line-height: 42px;
    letter-spacing: 0.02em;
    text-align: left;
    text-transform: uppercase;
    color: $green;
    @include screen(laptop) {
      font-size: 40px;
      line-height: 52px;
    }
    @include screen(laptop) {
      font-size: 56px;
      line-height: 73px;

      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      background-image: linear-gradient(135.29deg, #30446A 17.09%, #5787E2 80.7%);
    }
  }
  &__description {
    margin-top: 12px;
    font-size: 14px;
    line-height: 18px;
    max-width: 217px;
    margin-left: 39px;
    span {
      font-weight: 500;
    }
    @include screen(tablet) {
      margin-top: 20px;
      margin-left: 75px;
      max-width: 260px;
    }
    @include screen(laptop) {
      margin-left: 171px;
      margin-top: 7px;
      font-size: 16px;
      line-height: 21px;
      max-width: 300px;

    }
  }

  .swiper {
    &-button {
      &-prev,
      &-next {
        top: 50%;
        transform: translateY(-50%);
        width: 59px;
        height: 59px;
        background-color: $green;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        @include screen(tablet) {
          top: auto;
          bottom: 47px;
          transform: translateY(0);
        }
        @include screen(laptop) {
          width: 79px;
          height: 79px;
          bottom: 54px;
          background-color: $black-400;
          transition: background-color $transition-default;
          &:hover {
            background-color: $black-hover;
          }
        }
        &:after {
          display: none;
        }
        svg {
          width: 25px;
          @include screen(laptop) {
            width: 33px;
          }
        }
      }
      &-next {
        right: 102px;
        @include screen(tablet) {
          right: auto;
          left: 42%;
        }
        @include screen(laptop) {
          left: 41%;
        }
      }
      &-prev {
        display: none;
        svg {
          transform: rotate(180deg);
        }
      }

    }
    &-pagination {
      &-bullet {
        background-color: $white;
        width: 5px;
        height: 5px;
        opacity: 1;
        @include screen(laptop) {
          background-color: $black-400;
          width: 6px;
          height: 6px;

        }
        &-active {
          position: relative;
          &:before {
            position: absolute;
            content: '';
            width: 9px;
            height: 9px;
            top: -2px;
            left: -2px;
            border: 1px solid $white;
            background-color: transparent;
            border-radius: 50%;
            @include screen(laptop) {
              border: 1px solid $black-400;
              width: 12px;
              height: 12px;
              top: -3px;
              left: -3px;
            }
          }
        }
      }
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0 4px;
      @include screen(laptop) {
        margin: 0 6px;
      }
    }
    .swiper-pagination-fraction, .swiper-pagination-custom, .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal {
      display: flex;
      justify-content: center;
      top: 50%;
      bottom: auto;
      transform: translateY(-50%);
      right: 57px;
      width: auto;
      left: auto;
      @include screen(tablet) {
        top: auto;
        bottom: 72px;
        right: 41%;
        left: auto;
        transform: translateX(-50%);
      }
      @include screen(laptop) {
        bottom: 90px;
        right: 45%;
      }
    }
  }
}