.main-new {
  overflow: hidden;
  padding: 30px 0;
  @include screen(laptop) {
    padding: 50px 0;
  }
  .wrapper {
    overflow: hidden;
    padding-top: 12px;
    @include screen(tablet) {
      padding-top: 25px;
    }
    @include screen(laptop) {
      padding-top: 12px;
    }
  }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__navigation {
    display: flex;
    position: relative;
    align-items: center;
    gap: 10px
  }

  &__title {
    display: flex;
    align-items: center;
    gap: 6px;
    span {
      font-family: 'Tenor Sans';
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
      opacity: 0.3;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }

  h2 {
    position: relative;
  }
  &__amount {
    position: absolute;
    content: '';
    left: 100%;
    bottom: 100%;

    font-family: 'Tenor Sans';
    font-size: 10px;
    line-height: 13px;
    letter-spacing: 0.02em;
  }

  .swiper {
    overflow: visible;
    margin-top: 16px;
    @include screen(tablet) {
      margin-top: 24px;
    }
    @include screen(laptop) {
      margin-top: 32px;
    }
  }

  .swiper-button {
    &-prev,
    &-next {
      display: none;
      margin: 0;
      width: 45px;
      height: 45px;
      border-radius: 50%;
      border: 1px solid $black-400;
      background-color: $black-400;
      align-items: center;
      justify-content: center;
      top: auto;
      left: auto;
      right: auto;
      position: relative;
      @include screen(tablet) {
        display: flex;
        transition: background-color $transition-default;
        &:hover {
          background-color: $black-hover;
        }
      }
      svg {
        width: 19px;
      }
      &:after {
        display: none;
      }
    }
    &-prev {
      svg {
        transform: rotate(180deg);
      }
    }
    &-disabled {
      background-color: $white;
      svg {
        path {
          fill: $black-400;
        }
      }
    }
  }
  .swiper-button-prev.swiper-button-disabled, .swiper-button-next.swiper-button-disabled {
    opacity: 1;
  } 
}