.breadcrumbs {
  padding-bottom: 20px;
  @include screen(tablet) {
    padding-top: 10px;
  }
  @include screen(laptop) {
    padding-top: 40px;
    padding-bottom: 30px;

  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 5px;
    li {
      display: inline-flex;
      gap: 5px; 
    }
    a, 
    span, 
    p {
      font-size: 14px;
      line-height: 18px;
      color: $gray-500;
    }
  }
}