.main-brands {
  padding-bottom: 30px;
  @include screen(laptop) {
    padding-bottom: 50px;
  }
  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(tablet) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__item {
    grid-column-start: 1;

    display: block;
    border: 1px solid $gray;
    border-width: 0 0 1px 1px;
    padding: 20px 20px 20px 15px;
    @include screen(tablet) {
      grid-column-start: auto;

      display: flex;
      align-items: center;
      justify-content: center;
      padding: 22px;
    }
    @include screen(laptop) {
      padding: 42px;
    }
    span {
      font-family: 'Tenor Sans';
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      @include screen(tablet) {
        font-size: 12px;
        line-height: 18px;
      }
      @include screen(laptop) {
        font-size: 22px;
        line-height: 29px;
      }
    }
    &-large {
      grid-row: 1 / 4;
      grid-column-start: 2;

      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid $gray;
      border-width: 0 1px 1px 1px;
      position: relative;
      @include screen(tablet) {
        grid-row: auto;
        grid-column-start: auto;
        border-width: 0 0 0 1px;
      }
      @include screen(laptop) {
        border-width: 0 1px 1px 1px;
      }
      &:before {
        position: absolute;
        content: '';
        border-radius: 50%;
        width: 134px;
        height: 134px;
        border: 1px solid $gray;
        background-color: transparent;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @include screen(laptop) {
          width: 256px;
          height: 256px;
          transition: border-color $transition-default;
        }
      }
      &:hover {
        @include screen(laptop) {
          &:before {
            border-color: $black-hover;
          }
          .main-brands__button {
            background-color: $black-hover
          }
        }
      }
      
    }
    &-hidden {
      span {
        @include screen(tablet) {
          display: none;
        }
      }
    }
  }
  &__button {
    width: 30px;
    height: 30px;
    background-color: $black-400;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 30px;
    bottom: 30px;
    @include screen(tablet) {
      bottom: 10px;
      right: 35px;
    }
    @include screen(laptop) {
      width: 57px;
      height: 57px;
      right: 50px;
      bottom: 15px;
      transition: border-color $transition-default;
    }
    svg {
      width: 13px;
      @include screen(laptop) {
        width: 23px;
      }
    }
  }
  &__image {
    display: none;
    @include screen(tablet) {
      display: block;
    }
  }
}