.header-desktop {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: -20;
  overflow-y: hidden;
  width: 100%;
  height: 100%;
  transition: opacity $transition-default, visibility $transition-default, z-index $transition-default;
  &.active {
    opacity: 1;
    visibility: visible;
    position: relative;
    z-index: 1;

  }
}